body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Cocon';
  src: url(/static/media/Cocon-RegularTR.3cb2dc7f.ttf);
}

body {
  height: 1000px;
}

p, h2 {
  font-family: 'Cocon', sans-serif;
}

a {
  text-decoration: none;
}

.App-link {
  color: #61dafb;
}

.main {
  text-align: center;
}

.header {
  background-color: #282c34;
  min-height: 94vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.cvSection {
  background-color: darkgrey;
  display: flex;
  justify-content: center;
  flex-direction: column;
  min-height: 140vh;
}

.schoolSection {
  padding-top: 6vmin;
  background-color: lightgrey;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/*Header elements*/

.main-logo {
  height: 25vmin;
}

.title {
  color: #61dafb;
  font-family: 'Cocon', sans-serif;
  font-size: 5vmin;
}

.subtitle {
  padding-left: 6em;
  font-family: 'Cocon', sans-serif;
  font-size: 5vmin;
  color: #61dafb;
}

.subtitle-first {
  /*align-self: flex-end;*/
  margin-bottom: 3em;
}

.subtitle-last {

}

.word {
  position: absolute;
  max-width: 350px;
  opacity: 0;
  font-family: 'Cocon', sans-serif;
  font-size: 5vmin;
}

.letter {
  display: inline-block;
  position: relative;
  float: left;
  -webkit-transform: translateZ(25px);
          transform: translateZ(25px);
  -webkit-transform-origin: 50% 50% 25px;
          transform-origin: 50% 50% 25px;
}

.letter.space {
  margin-right: .3em;
}

.letter.out {
  -webkit-transform: rotateX(90deg);
          transform: rotateX(90deg);
  -webkit-transition: -webkit-transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: -webkit-transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  transition: transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19), -webkit-transform 0.32s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.letter.out.space {
  margin-right: .3em;
}

.letter.behind {
  -webkit-transform: rotateX(-90deg);
          transform: rotateX(-90deg);
}

.letter.behind.space {
  margin-right: .3em;
}

.letter.in {
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
  -webkit-transition: -webkit-transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: -webkit-transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275), -webkit-transform 0.38s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.letter.in.space {
  margin-right: .3em;
}

.wisteria {
  color: #8e44ad;
  margin-left: 1.5em;
}

.belize {
  color: #2980b9;
}

.pomegranate {
  color: #c0392b;
}

.green {
  color: #16a085;
  margin-left: -1.5em;
}

.midnight {
  color: #2c3e50;
}

.top-logos {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: baseline;
}

#email-logo {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 44vmin;
}

#email-logo:hover #email-txt {
  opacity: 1;
}

#git-logo {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 44vmin;
}

#git-logo:hover #git-txt {
  opacity: 1;
}

#email-txt {
  opacity: 0;
  -webkit-transition: .5s;
  transition: .5s;
  color: #61dafb;
  font-size: calc(5px + 2vmin);
  cursor: pointer;
}

#git-txt {
  opacity: 0;
  -webkit-transition: .5s;
  transition: .5s;
  color: #61dafb;
  font-size: calc(5px + 2vmin);
  cursor: pointer;
}

/*NAVBAR*/

.nav {
  overflow: hidden;
  background-color: #282c34;
}

.nav a {
  float: left;
  display: block;
  text-align: center;
  padding: 1vmin;
  margin-top: 1vmin;
  color: #61dafb;
  cursor: pointer;
  border-bottom: solid;
}

.nav-link:after {
  width: 0;
  right: 0;
  -webkit-transition: width .2s ease;
  transition: width .2s ease;
}

.underline:after {
  width: 100%;
  left: 0;
}

.nav img {
  float: left;
  display: block;
  height: 4vmin;
  padding: 1vmin;
  cursor: pointer;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}


.sticky + .cvSection {
  padding-top: 6vmin;
}


/*CV Section elements*/


.dlCv {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.flags {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.texts {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dl {
  display: flex;
  flex-direction: column;
  padding: 2vmin;
  border: solid;
  border-radius: 2vmin;
  border-color: #6981bd;
  background-color: white;
  margin-left: 5vmin;
  margin-bottom: 5vmin;
}

.cv {
  height: 100vmin;
  padding: 5vmin;
  margin-top: 5vmin;
  margin-bottom: 5vmin;
  margin-left: 5vmin;
  border: solid;
  border-radius: 2vmin;
  border-color: #6981bd;
  background-color: white;
}

.dl-txt {
  color: #6981bd;
  font-family: 'Cocon', sans-serif;
  font-size: 5vmin;
}

.dl-icon {
  height: 5vmin;
  margin-left: 2vmin;
}

.flag {
  cursor: pointer;
  height: 10vmin;
  margin-left: 5vmin;
  margin-bottom: 3vmin;
  margin-right: 5vmin;
  padding: 2vmin;
  border-radius: 20vmin;
}

.small-logo {
  height: 6vmin;
  cursor: pointer;
}

